/*        */
.toggle-mnu {
  display: none;
  width: 28px;
  height: 28px;
  line-height: 48px;
  text-align: center;
  color: #000000; }

.toggle-mnu span::after,
.toggle-mnu span::before {
  content: "";
  position: absolute;
  left: 0;
  top: 9px; }

.toggle-mnu span::after {
  top: 18px; }

.toggle-mnu span {
  position: relative;
  display: block; }

.toggle-mnu span,
.toggle-mnu span::after,
.toggle-mnu span::before {
  border-radius: 2px;
  width: 100%;
  height: 2px;
  background-color: #ffffff;
  backface-visibility: hidden;
  transition: all 0.3s; }

.toggle-mnu.on span {
  background-color: transparent; }

.toggle-mnu.on span::before {
  transform: rotate(45deg) translate(-1px, 0); }

.toggle-mnu.on span::after {
  transform: rotate(-45deg) translate(6px, -7px); }

.toggle-mnu.on + #menu {
